<template>
  <template v-if="isLoaded">
    <StatusBar :farm="farm"></StatusBar>
    <div class="table-div">
      <h2 class="table-header">Income Statement</h2>
      <table class="styled-table">
        <thead>
          <tr>
            <th class="name-cell">Assets</th>
            <th class="number-cell">Cashflow ($/day)</th>
            <th class="number-cell">Lifetime Total ($)</th>
          </tr>
        </thead>
        <tbody>
        <tr>
          <th class="name-cell">Sales</th>
          <th class="number-cell">{{ (farm.productionValue * 60).toFixed(0) }}</th>
          <th class="number-cell">{{ farm.state.milkSold.toFixed(0) }}</th>
        </tr>
        <tr>
          <td class="name-cell tier-2">Milk</td>
          <td class="number-cell">{{ (farm.productionValue * 60).toFixed(0) }}</td>
          <td class="number-cell">{{ farm.state.milkSold.toFixed(0) }}</td>
        </tr>

        <tr>
          <th class="name-cell">Employees</th>
          <th class="number-cell">{{ (-farm.currentSalaries * 60).toFixed(0) }}</th>
          <th class="number-cell">{{ -farm.lifetimeSalaries.toFixed(0) }}</th>
        </tr>
        <tr v-for="employee in farm.state.employees" v-bind:key="employee">
          <td class="name-cell tier-2">{{ employee.name }}</td>
          <td class="number-cell">{{ (-employee.salary.value * 60).toFixed(0) }}</td>
          <td class="number-cell">{{ -employee.money.toFixed(0) }}</td>
        </tr>

        <tr>
          <th class="name-cell">R&D</th>
          <th class="number-cell">0</th>
          <th class="number-cell">{{ -farm.researchSpending.toFixed(0) }}</th>
        </tr>

        <tr>
          <th class="name-cell">Sum</th>
          <td class="number-cell">{{ ((Math.round(farm.productionValue * 60) - Math.round(farm.currentSalaries * 60))) }}</td>
          <th class="number-cell">{{ farm.state.money.toFixed(0) }}</th>
        </tr>
        </tbody>
      </table>
    </div>

    <br><br>
    <div class="status-card saves-card">
      <div class="stat-bars">
        <div class="stat-row">
          <input v-model="farm.state.saveName">
          <TextButton @click="writeSave(farm.state.saveName)"><p>Save</p></TextButton>
        </div>

        <div class="stat-row saves-list">
          <TextButton v-for="(save, saveName) in readSaves()" :key="saveName"
                      @click="loadSave(saveName)"
                      :class="{'new-game-button': saveName === 'New Game'}"
          >
            <p>{{ saveName }}</p>
          </TextButton>
        </div>

        <div class="stat-row clear-all-button-row">
          <TextButton @click="farm.clearSaves(); $forceUpdate()"><p>Clear All</p></TextButton>
        </div>
      </div>
    </div>
  </template>
</template>

<script lang="ts">
import { Options, Vue } from 'vue-class-component'
import Farm from '@/Farm'
import EmployeeCard from '@/components/EmployeeCard.vue'
import ValueMeter from '@/components/ValueMeter.vue'
import util from '../util'
import FarmStatusCard from '@/components/FarmStatusCard.vue'
import StatusBar from '@/components/StatusBar.vue'
import TextButton from '@/components/TextButton.vue'

@Options({
  components: {
    EmployeeCard, ValueMeter, FarmStatusCard, StatusBar, TextButton
  },
  props: {
    farm: Object
  },
  methods: {
    log(msg: any) {
      console.log(msg)
    },
    util() {
      return util
    },
    readSaves() {
      return this.farm.readSavesFromLocalStorage()
    },
    loadSave(saveName: string) {
      this.farm.loadFromLocalStorage(saveName)
    },
    writeSave(saveName: string) {
      this.farm.save(this.farm.state.saveName)
      this.forceUpdate()
    },
    forceUpdate() {
      this.$forceUpdate()
    }
  },
  async created() {
    //let farm = new Farm()
    //await farm.load()
    //this.farm = farm
    this.isLoaded = true
  }
})
export default class AdminView extends Vue {
  isLoaded = false
  farm!: Farm
}
</script>

